/* Main container for the hackathon page */
.hackathon-page {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  margin: 30px 0;
  padding: 0 20px;
  font-size: 1.2rem;
}

/* Main Content Container (Center everything) */
.main-content {
  max-width: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 20px;
  box-sizing: border-box;
}
/* Tags Section */
.tags-section {
  width: auto;
  display: flex;
  flex-direction: column;
  gap: 15px;
  align-self: self-start;
  margin: 0 auto;
  align-items: center;
}

.tags-section-title {
  font-size: 1.5rem;
  font-weight: bold;
  color: #021427;
  margin-bottom: 15px;
}

.tags-container {
  display: flex;
  gap: 15px;
}

.tag {
  background-color: #021427;
  color: white;
  padding: 10px 20px;
  border-radius: 20px;
  font-size: 1rem;
  font-weight: bold;
  text-align: center;
  transition: background-color 0.3s, transform 0.3s ease;
  cursor: pointer;
}

.tag:hover {
  background-color: #ff9900;
  transform: scale(1.05);
}

/* Section Containers */
.section {
  margin-bottom: 40px;
  text-align: left;
  width: 100%;
  color: #0b0521;
  font-size: 1rem;
}

/* Hackathon Overview Section */
.overview {
  font-size: 1.2rem;
  line-height: 1.6;
  color: #333;
  max-width: 100%;
  width: 90%;
  margin: 0 auto;
}

/* Style for Download Dataset button */
.about-link.download-btn {
  background-color: #f12a2a;
  color: white;
  font-weight: bold;
  cursor: pointer;
  border: none;
  padding: 10px 10px;
  border-radius: 8px;
  transition: background-color 0.3s ease;
  margin-bottom: 20px;
}

.about-link.download-btn:hover {
  background-color: #bc0707;
}

/* Aims Section*/
.aims {
  margin-bottom: 40px;
  text-align: left;
  width: 100%;
  color: #0b0521;
}

.aims h3 {
  font-size: 1.5rem;
  margin-bottom: 10px;
  font-weight: bold;
}

.aims p {
  font-size: 1rem;
  margin-bottom: 20px;
  line-height: 1.6;
}

.aims h4 {
  font-size: 1.3rem;
  margin-top: 20px;
  margin-bottom: 10px;
  font-weight: bold;
}

.aims ul {
  list-style-type: disc;
  padding-left: 20px;
  max-width: 100%;
  width: 90%;
  margin: 0 auto;
}

.aims li {
  margin-bottom: 10px;
  font-size: 1rem;
  color: #0b0521;
  line-height: 1.8;
}

/* Rules Section */
.rules ul {
  padding-left: 20px;
  max-width: 900px;
  margin: 0 auto;
}

.rules li {
  color: #555;
  margin-bottom: 10px;
  color: #0b0521;
}

/* Awards Section */
.awards ul {
  padding-left: 20px;
  max-width: 900px;
  margin: 0 auto;
  color: #0b0521;
}

.awards li {
  color: #555;
  margin-bottom: 10px;
  color: #0b0521;
}

.digital-language-inclusion {
  font-size: 1.1rem;
  text-align: center;
  line-height: 1.6;
  color: #333; /* Standard readable color */
  max-width: 80%;
  margin: 0 auto;
}

.digital-language-inclusion a {
  color: #004080; /* Deep blue for a professional look */
  font-weight: bold;
  text-decoration: none; /* Removes underline */
  transition: color 0.3s ease, transform 0.2s ease;
}

/* Ensures visited links remain the same color */
.digital-language-inclusion a:visited {
  color: #004080; /* Keeps same color even after clicking */
}

/* Hover effect */
.digital-language-inclusion a:hover {
  color: #ff9900; /* Changes color on hover */
  transform: scale(1.05); /* Slight zoom effect */
}

/* Active state (when clicking) */
.digital-language-inclusion a:active {
  color: #ff6600; /* Slightly darker orange on click */
}

/* Sponsors Section */
.sponsors {
  display: flex;
  gap: 20px;
  justify-content: center;
  justify-self: center;
  justify-items: center;
  align-items: center;
  margin-top: 20px;
  width: 100%;
}
.prizes-list {
  font-weight: bold;
  font-size: 0.8rem;
}

.sponsors a {
  text-decoration: none;
}
.sponsors h2 {
  font-size: 1rem;
  text-align: center;
  color: rgb(6, 0, 0);
  text-transform: uppercase;
  opacity: 0.4;
}

.sponsors img {
  max-width: 100px;
  height: auto;
  object-fit: contain;
  border-radius: 8px;
}

.about-1 {
  font-size: 1rem;
}
.sponsors .euphonia {
  font-family: "Playfair Display", serif;
  font-size: 1rem;
  font-weight: 600;
  padding: 15px 30px;
  border-radius: 8px;
  display: inline-block;
  color: #333;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 2px;
  transition: all 0.3s ease;
}

.euphonia:hover {
  color: #6b46c1;
  transform: scale(1.05);
}

/* Register Card container to hold both the card and QR code */
.register-card-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 40px;
}

/* Styling for the registration card */
.register-card {
  width: 70%;
  max-width: 500px;
  padding: 20px;
  border-radius: 8px;
  background-color: #0b0521;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  text-align: center;
  margin-bottom: 20px;
  color: white;
}

/* Styling for the QR Code container */
.qr-code-container {
  width: 70%;
  max-width: 500px;
  border-radius: 8px;
  text-align: center;
  color: #0b0521;
}

.qr-code-container h3 {
  margin-bottom: 10px;
  /* Space between heading and the QR code */
}

.register-card h3 {
  font-size: 1.5rem;
  margin-bottom: 20px;
}

.register-card .register-button {
  background-color: #ff9900;
  padding: 10px 20px;
  color: white;
  text-align: center;
  font-size: 1.1rem;
  font-weight: bold;
  text-decoration: none;
  border-radius: 5px;
  display: block;
  margin-top: 20px;
  transition: background-color 0.3s;
}

.register-card .register-button:hover {
  background-color: #e68900;
}

/* Medium screens (tablets and below) */
@media (max-width: 1060px) {
  .hackathon-page {
    padding: 0 10px;
  }

  .main-content {
    margin: 0 auto;
    padding: 0 10px;
  }

  .carousel-container {
    padding: 10px;
    /* Adjust carousel padding */
  }

  .register-card-container {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    width: 40%;
  }

  .register-card,
  .qr-code-container {
    width: 80%;
  }
}

/* Small screens (phones) */
@media (max-width: 768px) {
  .hackathon-page {
    flex-direction: column;
    align-items: center;
  }

  .hackathon-page p {
    text-align: left !important;
  }

  .overview {
    margin-left: 15%;
    width: 100%;
    max-width: 80%;
  }

  .main-content {
    width: 100%;
    padding: 0 5%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .awards {
    margin-left: 10%;
    padding: 0 5%;
    width: 80%;
  }

  .section {
    padding: 10px;
  }

  .register-card-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 100%;
    gap: 20px;
  }

  .register-card {
    height: 5%;
  }

  .qr-code-container {
    width: 50%;
  }
  .register-card {
    width: 40%;
  }
}

@media (max-width: 450px) {
  .carousel-container {
    width: 100%;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

  .tags-section {
    width: auto;
    display: flex;
    flex-direction: column;
    gap: 15px;
    align-self: self-start;
    margin: 0 auto;
    align-items: center;
  }

  .tags-container {
    display: flex;
    gap: 5px;
  }

  .tag {
    font-size: 0.5rem;
  }

  .sponsors {
    flex-direction: row;
    align-items: center;
    width: 100%;
    box-sizing: border-box;
    justify-content: flex-start;
  }

  .sponsors .euphonia {
    font-size: 10px;
  }

  .sponsors img,
  .sponsors a {
    max-width: 10%;
    height: auto;
    text-align: center;
  }

  .sponsors h2 {
    font-size: 1.1rem;
    margin-bottom: 10px;
    text-align: center;
  }
}
@media (max-width: 400px) {
  .register-card-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 15px;
    width: 100%;
  }

  .register-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 90%;
    max-width: 300px;
    padding: 10px;
    box-sizing: border-box;
  }

  .countdown-timer {
    order: -1;
    margin-bottom: 10px;
    text-align: center;
  }

  .countdown-timer h2 {
    font-size: 1rem;
  }

  .register-button-qr {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 10px;
    width: 100%;
  }

  .register-button {
    display: inline-block;
    text-align: center;
    padding: 8px 12px;
    font-size: 0.9rem;
    font-weight: bold;
    border-radius: 5px;
    background-color: #ff9900;
    color: white;
    text-decoration: none;
    transition: background-color 0.3s ease;
  }

  .register-button:hover {
    background-color: #e68900;
  }

  .qr-code-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
  }

  .qr-code-container h3 {
    font-size: 0.9rem;
    margin-bottom: 5px;
  }
}
