/* Hackathon Page paragraph styling */
.hackathon-page p {
  text-align: justify;
}

/* Timeline container */
.timeline-container {
  max-width: 1000px;
  width: 100%;
  padding: 20px 0;
  margin-top: 50px;
  margin-left: 2%;
}

/* Timeline header styling */
.timeline-header {
  font-size: 24px;
  font-weight: bold;
  text-align: left;
  margin-bottom: 20px;
}

/* Timeline layout */
.timeline {
  position: relative;
  padding-left: 40px;
}

.timeline::before {
  content: '';
  position: absolute;
  left: 18px;
  top: 0;
  bottom: 0;
  width: 2px;
  background: #e0f2e9;
}

/* Timeline dot */
.timeline-dot {
  position: absolute;
  left: -40px;
  width: 36px;
  height: 36px;
  background: #e0f2e9;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Timeline item styling */
.timeline-item {
  margin-bottom: 30px;
  /* Reduced space between items */
}

.check-icon {
  width: 18px;
  height: 18px;
  color: #0d5f3b;
}

/* Timeline content box */
.timeline-content {
  background: #f5f5f5;
  padding: 15px;
  border-radius: 8px;
  margin-left: 60px;
}

/* Timeline dates styling */
.timeline-dates {
  font-size: 14px;
  color: #666;
  margin-bottom: 10px;
}

.timeline-dates p {
  margin: 5px 0;
}

/* Timeline title */
.timeline-title {
  font-size: 18px;
  font-weight: 600;
  color: #333;
  margin: 0 0 15px 0;
}

/* Timeline description */
.timeline-description {
  color: #444;
  line-height: 1.6;
  margin: 0;
}

/* Responsive Design */
@media (max-width: 768px) {

  .timeline-container {
    width: 100%;
    margin-left: 15%;
    margin-top: 50px;
  }
  

  .timeline-header {
    font-size: 1.5rem;
    margin-bottom: 10px;
  }

  .timeline {
    width: 70%;
  }

  .timeline::before {
    left: 40px;
  }

  .timeline-dot {
    left: -2px;
    width: 30px;
    height: 30px;
  }

  .check-icon {
    width: 16px;
    height: 16px;
  }

  .timeline-content {
    padding: 10px;
    margin-left: 50px;
    /* Adjust content space */
  }

  .timeline-title {
    font-size: 1.2rem;
    /* Smaller font size for mobile */
  }

  .timeline-description {
    font-size: 0.9rem;
    /* Slightly smaller font for readability */
  }

  /* Adjust margin for better spacing between timeline items */
  .timeline-item {
    margin-bottom: 20px;
  }
}

@media (max-width: 1060px) {
    .timeline-container {
      /* width: 100%; */
      margin-top: 50px;
    }
  
  
    .timeline-header {
      font-size: 1.5rem;
      margin-bottom: 10px;
    }
  
    .timeline::before {
      left: 40px;
    }
  
    .timeline-dot {
      left: -2px;
      width: 30px;
      height: 30px;
    }
  
    .check-icon {
      width: 16px;
      height: 16px;
    }
  
    .timeline-content {
      padding: 10px;
      margin-left: 50px;
    }
  
    .timeline-title {
      font-size: 1.2rem;
    }
  
    .timeline-description {
      font-size: 0.9rem;
    }
  
    /* Adjust margin for better spacing between timeline items */
    .timeline-item {
      margin-bottom: 20px;
    }
}

@media (max-width: 645px) {
  .timeline {
      width: 90%;
    }
}